.head {
    text-align: center;
    font-family: 'Hurricane', cursive;;
    margin: 2em;
    margin-top: 1em;
    font-size: 35px;
}

.head h3 {
    margin: 0.5em;
}

.factDiv {
    font-family: 'Bebas Neue', cursive;
    text-align: center;
    font-size: 24px;
    width: 90%;
    height: 4em;
    margin: 0px auto;
    font-weight: bolder;
}

.head img {
    border: 5px solid black;
    box-shadow: 10px 6px 9px #000000;;
}

.btnDiv {
    display: flex;
    justify-content: center;
}

.btn {
    position: fixed;
    margin-top: 0.6em;
    width: 50px;
}